import { useMemo } from 'react';
import { EntityPageConfig } from '../types';

export const useEntityPageHeaderTexts = ({
  config,
  isCreateMode,
  entityDisplayName,
}: {
  config: EntityPageConfig;
  isCreateMode: boolean;
  entityDisplayName?: string;
}) => {
  const title = useMemo(() => {
    if (config.title) {
      return config.title;
    } else {
      return {
        text: isCreateMode
          ? 'Create New Item'
          : entityDisplayName ?? 'Edit Item',
      };
    }
  }, [config.title, entityDisplayName, isCreateMode]);

  const subtitle = useMemo(() => config.subtitle, [config.subtitle]);

  return { title, subtitle };
};
